@use 'base/variables' as v;
@use 'base/mixins' as m;


.proyectos{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: v.$blanco;
    min-height: 100vh;
    max-width: 900px;
    width: 95%;
    overflow: hidden;
    padding: 5rem 5rem;
    margin: 2rem auto;
    border-radius: 8px;
    box-shadow: 0 0 100px rgba(255, 255, 255, 40);

    @include m.tablet{
        max-width: 600px;
        width: 95%;
    }

    @include m.telefono{
        max-width: 400px;
        width: 95%;
    }
}

.content2{
    display: flex;
    gap: 5rem;
    align-items: center;
    background: linear-gradient(180deg, rgba(2, 2, 2, 0.3) 0%, rgba(255,255,255,0) 100%);
    backdrop-filter: blur(30px);
    border-radius: 20px;
    max-width: 750px;
    box-shadow: 0 0.5px 0 1px rgba(255,255,255, 0.2) inset, 0 1px 0 0 rgba(255,255,255, 0.65) inset, 04px 16px rgba(0,0,0, 0.1);
    z-index: 10;
    padding: 3rem;

    @include m.tablet{
        display: flex;
        flex-direction: column;
        max-width: 600px;
        align-items: center;
        padding: 0;
        padding-bottom: 2rem; 
        width: 95%;
    }
}

.info2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align:  justify;

    .h22{
        margin-bottom: 3rem;

        @include m.tablet{
            margin: 0;
            margin-top: 3rem;
            padding-bottom: 3rem;
        }
    }

    .p2{
        color: v.$container_color;
        font-weight: 500;
        font-size: 1.1rem;
        line-height: 1.5;
        width: 300px;
        text-align: justify;
        
        @include m.telefono{
            text-align: center;
            width: 95%;
            font-size: 1rem;
            margin-left: .3rem;
        }
    }
}

.swiper{
    width: 250px;
    height: 450px;
    padding: 50px 0;   

    @include m.tablet{
        margin: 0;
    }
}
.swiper-slide2{
    position: relative;
    box-shadow: 0 15px 50px rgba(0,0,0,0.2);
    border-radius: 10px;
    width: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    
    .fa-brands{
        font-size: 3rem;
        color: v.$blanco;
        margin-bottom: 1rem;
    }
}
.swiper-slide h2{
    position: relative;
    bottom: 0;
    left: 0;
    padding-top: 40px;
    text-align: center;
    color: v.$blanco;
    font-weight: 400;
    font-size: 1.1rem;
    line-height: 1.4;
}
.swiper-slide2:nth-child(1n){
    background: linear-gradient(to top, #0f2027, #203a4300, #2c536400), url(/src/img/proyectos/Barberia.avif) no-repeat 50% 50% / cover;
}
.swiper-slide2:nth-child(2n){
    background: linear-gradient(to top, #0f2027, #203a4300, #2c536400), url(/src/img/proyectos/BienesRaices.avif) no-repeat 50% 50% / cover;
}
.swiper-slide2:nth-child(3n){
    background: linear-gradient(to top, #0f2027, #203a4300, #2c536400), url(/src/img/proyectos/ConferenciasDev.avif) no-repeat 50% 50% / cover;
}
.swiper-slide2:nth-child(4n){
    background: linear-gradient(to top, #0f2027, #203a4300, #2c536400), url(/src/img/proyectos/UpTaskCrud.avif) no-repeat 50% 50% / cover;
}